import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);
const homePage = r => require.ensure([], () => r(require('../components/home_page/homePage.vue')), 'homePage');
const userCenter = r => require.ensure([], () => r(require('../components/user_center/userCenter.vue')), 'userCenter');
const assetDetail = r => require.ensure([], () => r(require('../components/user_center/assetDetail.vue')), 'assetDetail');
const walletManage = r => require.ensure([], () => r(require('../components/user_center/walletManage.vue')), 'walletManage');
const entrustRecord = r => require.ensure([], () => r(require('../components/user_center/entrustRecord.vue')), 'entrustRecord');
const rollIn = r => require.ensure([], () => r(require('../components/user_center/rollIn.vue')), 'rollIn');
const rollOut = r => require.ensure([], () => r(require('../components/user_center/rollOut.vue')), 'rollOut');
const dealRecord = r => require.ensure([], () => r(require('../components/user_center/dealRecord.vue')), 'dealRecord');
const bindPhone = r => require.ensure([], () => r(require('../components/user_center/bindPhone.vue')), 'bindPhone');
const resetLoginPassWord = r => require.ensure([], () => r(require('../components/user_center/resetLoginPassWord.vue')), 'resetLoginPassWord');
const resetDealPassWord = r => require.ensure([], () => r(require('../components/user_center/resetDealPassWord.vue')), 'resetDealPassWord');
const conjuncture = r => require.ensure([], () => r(require('../components/deal/conjuncture.vue')), 'conjuncture');
const buySell = r => require.ensure([], () => r(require('../components/deal/buySell.vue')), 'buySell');
const entrust = r => require.ensure([], () => r(require('../components/deal/entrust.vue')), 'entrust');
const login = r => require.ensure([], () => r(require('../components/login_register/login.vue')), 'login');
const setPassWord = r => require.ensure([], () => r(require('../components/login_register/setPassWord.vue')), 'setPassWord');
const realName = r => require.ensure([], () => r(require('../components/login_register/realName.vue')), 'realName');
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/conjuncture',
      name: 'conjuncture',
      component: conjuncture,
      children: [
        {
          path: '/buySell',
          name: 'buySell',
          component: buySell
        },
        {
          path: '/entrust',
          name: 'entrust',
          component: entrust
        },
      ]
    },
    {
      path: '/resetDealPassWord',
      name: 'resetDealPassWord',
      component: resetDealPassWord
    },
    {
      path: '/resetLoginPassWord',
      name: 'resetLoginPassWord',
      component: resetLoginPassWord
    },
    {
      path: '/bindPhone',
      name: 'bindPhone',
      component: bindPhone
    },
    {
      path: '/dealRecord',
      name: 'dealRecord',
      component: dealRecord
    },
    {
      path: '/rollOut',
      name: 'rollOut',
      component: rollOut
    },
    {
      path: '/rollIn',
      name: 'rollIn',
      component: rollIn
    },
    {
      path: '/entrustRecord',
      name: 'entrustRecord',
      component: entrustRecord
    },
    {
      path: '/walletManage',
      name: 'walletManage',
      component: walletManage
    },
    {
      path: '/assetDetail',
      name: 'assetDetail',
      component: assetDetail
    },
    {
      path: '/userCenter',
      name: 'userCenter',
      component: userCenter
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      children: [
        {
          path: '/setPassWord',
          name: 'setPassWord',
          component: setPassWord,
          children: [
            {
              path: '/realName',
              name: 'realName',
              component: realName
            }
          ]
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  var user = window.localStorage.getItem('user');
  // console.log(JSON.parse(user).name);
  // console.log('to:' + to.path);
  if (to.path === '/login') {
    if (user) {
      next('/homePage')
    } else {
      next()
    }
  } else {
    next()
  }
});
export default router



